* {
  border: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

a {
  text-decoration: none;
  color: inherit;
}

:root {
  --color-white: rgb(220, 218, 218);
  --color-background: rgb(17, 17, 17);

  --color-dark: rgb(23, 24, 24);
  --color-green: rgb(92, 223, 92);
}

body {
  font-family: "Ubuntu", sans-serif; 
}